@import url(https://fonts.googleapis.com/css2?family=Smythe&display=swap);
body{margin:0;font-family:'Smythe', cursive;background:#fafafa}svg{display:block}.box{will-change:auto;background:#6B675E;-webkit-user-select:none;-ms-user-select:none;user-select:none}.box__dot{transition:all linear .2s;cursor:pointer}.box__dot:hover{fill:#FEDC7C}.box__dot--active{fill:#FEDC7C}.box__line{stroke-width:6;transition:all linear .5s;border-radius:100%}.box__tale{transition:all linear .5s}.box__number{font-size:1.5rem;pointer-events:none}.box__mouseline{pointer-events:none}.players{background:#49453d}.player{transform:translateY(50%)}.player__circle{fill:#35312a;stroke-width:5;transition:2s ease all}.player__score{font-size:64px}.player--current .player__circle{stroke-width:15;stroke-dashoffset:50}.winner__rect{fill:black;stroke:#FEDC7C;stroke-width:10;stroke-dasharray:10;-webkit-animation:winner linear 30s;animation:winner linear 30s}.winner__text{font-size:32px;width:500px;fill:#FEDC7C}@-webkit-keyframes winner{0%{stroke-dashoffset:0}100%{stroke-dashoffset:500}}@keyframes winner{0%{stroke-dashoffset:0}100%{stroke-dashoffset:500}}

